import 'ScssComponents/_popup.scss';

class Popup {
	get selectors() {
		return {
			popupOpen: '.popupOpen',
			popup: '.popupCard',
			popupContent: '.popupCardContent',
			popupClose: '.popupCardClose',
			lockPadding: '.lockPadding',
		};
	}

	constructor() {
		const $btn = document.querySelectorAll(this.selectors.popupOpen);
		const $popup = document.querySelector(this.selectors.popup);
		const $close = document.querySelectorAll(this.selectors.popupClose);
		const $body = document.body;
		const $lockPadding = document.querySelectorAll(this.selectors.lockPadding);

		this.$btn = $btn;
		this.$popup = $popup;
		this.$close = $close;
		this.$lockPadding = $lockPadding;
		this.$body = $body;
	}

	init() {
		let unlock = true;
		const timeout = 500;

		const bodyLock = () => {
			const lockPaddingValue = window.innerWidth - document.body.offsetWidth;

			if (this.$lockPadding.length > 0) {
				this.$lockPadding.forEach(item => {
					const el = item;
					el.style.paddingRight = `${lockPaddingValue}px`;
				});
			}
			this.$body.style.paddingRight = `${lockPaddingValue}px`;
			this.$body.classList.add('body--popup_open');

			unlock = false;
			setTimeout(() => {
				unlock = true;
			}, timeout);
		};

		const bodyUnLock = () => {
			setTimeout(() => {
				if (this.$lockPadding.length > 0) {
					this.$lockPadding.forEach(item => {
						const el = item;
						el.style.paddingRight = '0px';
					});
				}
				this.$body.style.paddingRight = '0px';
				this.$body.classList.remove('body--popup_open');
			}, timeout);

			unlock = false;
			setTimeout(() => {
				unlock = true;
			}, timeout);
		};

		// Закрытие попапа
		function popupClose(popupActive, doUnlock = true) {
			if (unlock) {
				popupActive.classList.remove('popup--open');
				if (doUnlock) {
					bodyUnLock();
				}
			}
		}

		// Закрытие попапа при нажатии Esc
		document.addEventListener('keydown', e => {
			if (e.which === 27) {
				const popupActive = document.querySelector('.popup--open');
				popupClose(popupActive);
			}
		});

		// Открытие попапа
		const popupOpen = (currentPopup) => {
			if (currentPopup && unlock) {
				const popupActive = document.querySelector('.popup--open');
				if (popupActive) {
					popupClose(popupActive, false);
				} else {
					bodyLock();
					currentPopup.classList.add('popup--open');
				}
				// Закрытие при нажатии на темную область
				currentPopup.addEventListener('click', e => {
					if (!e.target.closest(this.selectors.popupContent)) {
						popupClose(e.target.closest(this.selectors.popup));
						// bodyClass(false);
					}
				});
			}
		};

		if (!this.$btn.length && !this.$popup) return null;

		this.$btn.forEach(item => {
			const btnItem = item;
			btnItem.addEventListener('click', (e) => {
				const popupName = btnItem.getAttribute('href').replace('#', '');
				const currentPopup = document.getElementById(popupName);
				popupOpen(currentPopup);
				e.preventDefault();
			});
		});

		if (!this.$close) return null;

		this.$close.forEach(item => {
			const closeBtn = item;
			closeBtn.addEventListener('click', () => {
				popupClose(closeBtn.closest(this.selectors.popup));
			});
		});

		return null;
	}
}

export default Popup;
